<template>
  <view>
    <uv-form
      labelPosition="left"
      :model="form"
      :rules="rules"
      labelWidth="100"
      errorType="toast"
      ref="formRef"
    >
      <uv-form-item label="头像" prop="avatar" borderBottom ref="item1">
        <view v-if="visible == false" class="flex items-center justify-end">
          <uv-avatar :src="form.avatar" size="30" @click="replaceAvatar"></uv-avatar>
        </view>
        <view v-else>
          <uv-avatar
            :src="form.avatar"
            size="30"
            class="fr"
            @click="previewImage(form.avatar)"
          ></uv-avatar>
        </view>
      </uv-form-item>
      <uv-form-item label="姓名" prop="name" borderBottom ref="item1" required>
        <view v-if="visible == false" class="fr">
          <uv-input
            v-model="form.name"
            border="none"
            placeholder="请输入姓名"
            @blur="getInstructInfo"
          />
        </view>
        <view v-else class="fr">
          {{ form.name }}
        </view>
      </uv-form-item>
      <uv-form-item label="身份证号" prop="id_card" borderBottom ref="item1" required>
        <view v-if="visible == false" class="fr">
          <uv-input
            v-model="form.id_card"
            border="none"
            placeholder="请输入身份证号"
            @blur="getInstructInfo"
          />
        </view>
        <view v-else class="fr">
          {{ form.id_card }}
        </view>
      </uv-form-item>
      <uv-form-item label="指导项目" prop="tag_ids" borderBottom ref="item1">
        <view v-if="visible == false" @click="handleSelectProject" class="flex items-center">
          <uv-input readonly v-model="form.tagIdsStr" placeholder="请选择指导项目" border="none" />
          <uv-icon name="arrow-right" color="#969799"></uv-icon>
        </view>
        <view v-else class="fr">
          {{ form.tagIdsStr }}
        </view>
      </uv-form-item>
      <uv-form-item
        label="归属区域"
        prop="tagGroup"
        borderBottom
        ref="item1"
        required
        @click="handleClickSelect(2)"
      >
        <view v-if="visible == false" class="flex items-center">
          <uv-input readonly v-model="form.tagGroup" placeholder="请输入归属区域" border="none" />
          <uv-icon name="arrow-right" color="#969799"></uv-icon>
        </view>
        <view v-else class="fr">{{ form.tagGroup }}</view>
      </uv-form-item>
      <uv-form-item label="详细地址" prop="address" borderBottom ref="item1">
        <view v-if="visible == false">
          <uv-textarea
            v-model="form.address"
            border="none"
            placeholder="请输入详细地址"
            autoHeight
          ></uv-textarea>
        </view>
        <view v-else class="fr">
          {{ form.address }}
        </view>
      </uv-form-item>
      <uv-form-item label="手机号" prop="phone" borderBottom ref="item1" required>
        <view v-if="visible == false" class="fr">
          <uv-input v-model="form.phone" border="none" placeholder="请输入手机号" />
        </view>
        <view v-else class="fr">
          {{ form.phone }}
        </view>
      </uv-form-item>
      <uv-form-item label="民族" prop="nation" borderBottom ref="item1" required>
        <view v-if="visible == false" @click="handleClickSelect(3)" class="flex items-center">
          <uv-input readonly v-model="form.nationStr" placeholder="请选择民族" border="none" />
          <uv-icon name="arrow-right" color="#969799"></uv-icon>
        </view>
        <view v-else class="fr">
          {{ form.nationStr }}
        </view>
      </uv-form-item>

      <uv-form-item label="文化程度" prop="education_level" borderBottom ref="item1" required>
        <view v-if="visible == false" @click="handleClickSelect(4)" class="flex items-center">
          <uv-input
            readonly
            v-model="form.educationLevelStr"
            placeholder="请选择文化程度"
            border="none"
          />
          <uv-icon name="arrow-right" color="#969799"></uv-icon>
        </view>
        <view v-else class="fr">
          {{ form.educationLevelStr }}
        </view>
      </uv-form-item>

      <uv-form-item label="当前指导员级别" prop="level" borderBottom ref="item1">
        <view v-if="visible == false" @click="handleClickSelect(5)" class="flex items-center">
          <uv-input
            readonly
            v-model="form.levelStr"
            placeholder="请选择当前指导员级别"
            border="none"
          />
          <uv-icon name="arrow-right" color="#969799"></uv-icon>
        </view>
        <view v-else class="fr">
          {{ form.levelStr }}
        </view>
      </uv-form-item>

      <uv-form-item label="指导类型" prop="guide_type" borderBottom ref="item1" required>
        <view v-if="visible == false" class="fr">
          <uv-radio-group v-model="form.guide_type" placement="column">
            <uv-radio
              :customStyle="{ marginBottom: '8px' }"
              v-for="(item, index) in guideTypeList"
              :key="index"
              :label="item.label"
              :name="item.id"
              @change="radioChange"
            ></uv-radio>
          </uv-radio-group>
        </view>
        <view v-else class="fr">
          {{ form.guideTypeStr }}
        </view>
      </uv-form-item>

      <uv-form-item label="人员构成" prop="name" borderBottom ref="item1" required>
        <view v-if="visible == false" @click="handleClickSelect(6)" class="flex items-center">
          <uv-input
            readonly
            v-model="form.personnelFormStr"
            placeholder="请选择人员构成"
            border="none"
          />
          <uv-icon name="arrow-right" color="#969799"></uv-icon>
        </view>
        <view v-else class="fr">
          {{ form.personnelFormStr }}
        </view>
      </uv-form-item>

      <uv-form-item label="成为社体指导员时间" prop="name" borderBottom ref="item1">
        <view v-if="visible == false" class="flex items-center justify-end">
          <picker
            mode="date"
            :value="form.time"
            :start="startDate"
            :end="endDate"
            @change="bindDateChange($event, 1)"
            style="width: 100%"
          >
            <view class="uni-input" :style="form.time ? '' : 'color: rgb(192, 196, 204);'">
              {{ form.time || '请选择成为社体指导员时间' }}
            </view>
          </picker>
          <uv-icon name="arrow-right" color="#969799"></uv-icon>
        </view>
        <view v-else class="fr">
          {{ form.time }}
        </view>
      </uv-form-item>

      <uv-form-item label="现有等级授予时间" prop="now_level_grant_time" borderBottom ref="item1">
        <view v-if="visible == false" class="flex items-center fr justify-end">
          <picker
            mode="date"
            :value="form.now_level_grant_time"
            :start="startDate"
            :end="endDate"
            @change="bindDateChange($event, 2)"
            style="width: 100%"
          >
            <view
              class="uni-input"
              :style="form.now_level_grant_time ? '' : 'color: rgb(192, 196, 204);'"
            >
              {{ form.now_level_grant_time || '请选择现有等级授予时间' }}
            </view>
          </picker>
          <uv-icon name="arrow-right" color="#969799"></uv-icon>
        </view>
        <view v-else class="fr">
          {{ form.now_level_grant_time }}
        </view>
      </uv-form-item>

      <uv-form-item
        label="现有等级授予部门"
        prop="now_level_organ_unit_name"
        borderBottom
        ref="item1"
      >
        <view v-if="visible == false" class="flex items-center fr">
          <uv-input
            v-model="form.now_level_organ_unit_name"
            placeholder="请输入现有等级授予部门"
            border="none"
          />
        </view>
        <view v-else class="fr">
          {{ form.now_level_organ_unit_name }}
        </view>
      </uv-form-item>

      <uv-form-item label="地区分布" prop="area_range" borderBottom ref="item1" required>
        <view v-if="visible == false" @click="handleClickSelect(7)" class="flex items-center">
          <uv-input
            readonly
            v-model="form.areaRangeStr"
            placeholder="请选择地区分布"
            border="none"
          />
          <uv-icon name="arrow-right" color="#969799"></uv-icon>
        </view>
        <view v-else class="fr">
          {{ form.areaRangeStr }}
        </view>
      </uv-form-item>
    </uv-form>
    <uv-picker
      :show="showPicker"
      :columns="columns"
      keyName="label"
      ref="uvPickerRef"
      @confirm="handlePickerConfirm"
      @cancel="showPicker = false"
      @change="changeHandler"
      :key="currentPick"
      :defaultIndex="defaultIndex"
    ></uv-picker>
    <canvas
      canvas-id="canvas"
      id="canvas"
      style="visibility: hidden; position: fixed; top: -100000; left: -100000"
    ></canvas>
  </view>
</template>
<script>
import { rules } from './index'
import trainApply from '@/static/images/train-apply-bg.png'
import { formatTreeOption, flattenTree, filterArray } from '@/utils/tree'
import useOptions from '@/configs/useOption/index'
import { previewImage, chooseImage } from '@/utils/index'
const { educationLevelList, levelList, guideTypeList, areaRangeList } = useOptions()
export default {
  name: 'TrainApplyForm',
  props: {
    formData: {
      type: Object,
      default: () => ({}),
    },
    readonly: {
      type: Boolean,
      default: false,
    },
  },
  data() {
    return {
      previewImage: previewImage,
      pickTime: 1,
      visible: false,
      currentPick: 1,
      trainApply,
      rules,
      showTime: false,
      showPicker: false,
      form: {
        name: '',
        avatar: '',
        fileList: [],
      },
      tagGroupList: [],
      areaGroupList: [],
      nation: [],
      personnel: [],
      educationLevel: [],
      levelList: [],
      guideTypeList: [],
      areaRangeList: [],
      columns: [],
      pickSource: {},
      defaultIndex: [], //默认选中
      tempInfo: {},
    }
  },
  watch: {
    formData: {
      handler(value) {
        this.form = value
      },
    },
    readonly: {
      handler(value) {
        this.visible = Boolean(value)
      },
    },
  },
  created() {
    this.form = this.formData
    this.visible = Boolean(this.readonly)
    this.getTagGroupList()
    this.getAreaOption()
    this.getConfigSelect('nation').then((res) => {
      this.nation = res
      this.pickSource.nation = res
    })
    this.getConfigSelect('personnel_form').then((res) => {
      this.personnel = res
      this.pickSource.personnel = res
    })
    this.educationLevel = this.returnPickMap(educationLevelList, ['value', 'label'])
    this.levelList = this.returnPickMap(levelList, ['value', 'label'])
    this.guideTypeList = this.returnPickMap(guideTypeList, ['value', 'label'])
    this.areaRangeList = this.returnPickMap(areaRangeList, ['value', 'label'])
    this.pickSource = {
      ...this.pickSource,
      educationLevel: this.educationLevel,
      levelList: this.levelList,
      guideTypeList: this.guideTypeList,
      areaRangeList: this.areaRangeList,
    }
    console.log(this.$refs['formRef'], '------')
    this.$refs['formRef'] && this.$refs['formRef'].setRules(this.rules)
  },
  computed: {
    startDate() {
      return this.getDate('start')
    },
    endDate() {
      return this.getDate('end')
    },
  },
  mounted() {
    uni.$on('TAGOBJ', (data) => {
      this.form = {
        ...this.form,
        ...data,
      }
    })
  },
  methods: {
    handleSelectProject() {
      const tagObj = {
        tagIds: this.form.tagIds || this.form.tag_ids || '',
      }
      uni.navigateTo({
        url: '/pages-sub/selectActivetyProject/index',
        success: (res) => {
          res.eventChannel.emit('TAGOBJ', tagObj)
        },
      })
    },
    async getInstructInfo() {
      if (
        this.form.name &&
        this.form.id_card &&
        (this.form.name != this.tempInfo.name || this.form.id_card != this.tempInfo.id_card)
      ) {
        const params = {
          name: this.form.name,
          id_card: this.form.id_card,
        }
        this.tempInfo = params
        const res = await this.$api.getWxTrainActivityInstructInfo(params)
        if (res.code == 200 && Object.prototype.toString.call(res.data) === '[object Object]') {
          const resData = res.data
          const form = {
            ...resData,
            avatar: resData.avatar_str || resData.avatar,
            tagIdsStr: (resData.tag_ids_arr || [])
              .map((item) => {
                return item.tag_id_str
              })
              .join(','),
            tagGroup: `${resData.province_str}/${resData.city_str}/${resData.county_str}/${resData.street_str}/${resData.community_str}`,
            nationStr: resData.nation_str,
            educationLevelStr: resData.education_level_str,
            levelStr: resData.level_str,
            guide_type: resData.guide_type,
            guideTypeStr: resData.guide_type_str,
            personnelFormStr: resData.personnel_form_str,
            time:
              resData.first_become_time == 0
                ? ''
                : this.$dayjs(resData.first_become_time * 1000).format('YYYY-MM-DD'),
            now_level_organ_unit_name: resData.now_level_organ_unit_name,
            now_level_grant_time:
              resData.now_level_grant_time == 0
                ? ''
                : this.$dayjs(resData.now_level_grant_time * 1000).format('YYYY-MM-DD'),
            status_str:
              resData.status_str || this.$dict.getDictLabel('statusColor', resData.status, {}),
            areaRangeStr: resData.area_range_str,
          }
          this.form = {
            ...form,
            ...this.form,
          }
        } else {
          console.log(res.message)
        }
      }
    },
    // 日期选中变化函数
    bindDateChange: function (e, type) {
      if (type == 1) {
        this.form.time = e.detail.value
      } else {
        this.form.now_level_grant_time = e.detail.value
      }
    },
    // 获取当前日期
    getDate(type) {
      const date = new Date()
      let year = date.getFullYear()
      let month = date.getMonth() + 1
      let day = date.getDate()

      if (type === 'start') {
        year = year - 60
      } else if (type === 'end') {
        year = year + 2
      }
      month = (month > 9 ? month : '0') + month
      day = (day > 9 ? day : '0') + day
      return `${year}-${month}-${day}`
    },
    // handleTimePick(item) {
    // 	this.pickTime = item;
    // 	this.showTime = true;
    // },
    // 回显数据
    backfill(temp, type, level) {
      let index = 0
      let index1 = 0
      let index2 = 0
      let arr = []
      let arr1 = []
      let arr2 = []
      //匹配对应每列的第几个
      this.pickSource[type].forEach((item, i) => {
        if (item.id == temp[0]) {
          index = i
          if (level != 1) {
            item.children.forEach((val, ind) => {
              if (val.id == temp[1]) {
                index1 = ind
                if (level != 2) {
                  val.children.forEach((e, n) => {
                    if (e.id == temp[2]) {
                      index2 = n
                    }
                  })
                }
              }
            })
          }
        }
      })
      arr = this.pickSource[type].map((e) => {
        return { label: e.label, id: e.id, level: 0, parentId: 0 }
      })
      this.columns = [arr]
      this.defaultIndex = [index]
      if (level != 1) {
        arr1 = this.pickSource[type][index].children.map((e) => {
          return { label: e.label, id: e.id, level: 1, parentId: this.pickSource[type][index].id }
        })
        this.columns = [arr, arr1]
        this.defaultIndex = [index, index1]
        if (level != 2) {
          arr2 = this.pickSource[type][index].children[index1].children.map((e) => {
            return {
              label: e.label,
              id: e.id,
              level: 2,
              parentId: this.pickSource[type][index].children[index1].id,
            }
          })
          this.columns = [arr, arr1, arr2]
          this.defaultIndex = [index, index1, index2]
        }
      }
    },
    changeVisible() {
      this.visible = true
    },
    // 替换头像
    async replaceAvatar() {
      const res = await chooseImage('canvas')
      if (res.status) {
        this.$api.uploadPic({ filePath: res.file }).then((resp) => {
          let res = JSON.parse(resp.data)
          if (res.code == 200) {
            this.form.avatar = res.data.imgUrl
          } else {
            this.$toast(res.message)
          }
        })
      }
    },
    // 校验表单
    async validate() {
      this.$refs['formRef'] && this.$refs['formRef'].setRules(this.rules)
      let res = await this.$refs.formRef.validate()
      return res
    },
    radioChange(e) {},
    returnPickMap(data = [], key) {
      return data.map((item) => {
        return {
          id: item[key[0]],
          label: item[key[1]],
        }
      })
    },
    async getConfigSelect(type) {
      let params = {
        type: type,
      }
      let res = await this.$api.getConfigSelect(params)
      return this.returnPickMap(res.data, ['id', 'name'])
    },
    dealPickChangeReturn(columnData, level = 1, e) {
      let filterData1 = []
      let filterData2 = []
      if (level != 1) {
        filterData1 = this.getDefaultColumns(columnData, 1, e.indexs[0])
        if (filterData1.length == 0) {
          filterData1.push({
            id: '-',
            label: '无',
            parentId: columnData[0][e.indexs[0]].id,
          })
        }
        this.$refs['uvPickerRef'].setColumnValues(1, filterData1)
      }
      if (level == 3) {
        filterData2 = this.getDefaultColumns(columnData, 2, e.indexs[1])
        if (filterData2.length == 0) {
          filterData2.push({
            id: '-',
            label: '无',
            parentId: columnData[1][e.indexs[1]].id || '-',
          })
        }
        this.$refs['uvPickerRef'].setColumnValues(2, filterData2)
      }
    },
    changeHandler(e) {
      switch (this.currentPick) {
        case 1:
          this.dealPickChangeReturn(this.tagGroupList, 2, e)
          break
        case 2:
          this.dealPickChangeReturn(this.areaGroupList, 3, e)
          break
        case 3:
          console.log('民族')
          break
        case 4:
          console.log('文化程度')
          break
        case 5:
          console.log('当前指导员级别')
          break
        case 6:
          console.log('人员构成')
          break
        case 7:
          console.log('地区分布')
          break
        default:
          break
      }
    },
    async getTagGroupList() {
      let res = await this.$api.getTagGroupList({
        page: 1,
        size: 1000,
        type: 'social_sports_project_kind',
        is_show_tag_id: 1,
      })
      this.pickSource.tagGroup = formatTreeOption(res.data.list, [
        'name',
        'tag_group_id',
        'tag_id_str',
        'tag_id',
        'tag_id_arr',
      ])
      this.tagGroupList = flattenTree(this.pickSource.tagGroup)
    },
    // handleTimeConfirm(e) {
    // 	if (this.pickTime == 1) {
    // 		this.form.time = e[0];
    // 	} else {
    // 		this.form.now_level_grant_time = e[0];
    // 	}
    // 	this.showTime = false;
    // },

    // 根据父节点过滤数据
    getDefaultColumns(data = [], rowIndex, columnIndex = 0) {
      return data[rowIndex].filter((item) => {
        return item.parentId == data[rowIndex - 1][columnIndex].id
      })
    },
    handleClickSelect(type) {
      this.showPicker = true
      this.$refs['uvPickerRef'].open()
      this.currentPick = type
      let tagData = JSON.parse(JSON.stringify(this.tagGroupList))
      let areaData = JSON.parse(JSON.stringify(this.areaGroupList))
      switch (type) {
        case 1:
          if (this.form.tagIds) {
            this.backfill(this.form.tagIds, 'tagGroup', 2)
            return
          }
          if (this.form.tagIsNone) {
            this.backfill(this.form.tagIsNone, 'tagGroup', 2)
            return
          }
          tagData[1] = this.getDefaultColumns(tagData, 1)
          this.columns = tagData
          break
        case 2:
          if (this.form.tagGroup) {
            // 写死的省市区 后续需要更改
            this.backfill([this.form.county, this.form.street, this.form.community], 'areaGroup', 3)
            return
          }
          areaData[1] = this.getDefaultColumns(areaData, 1)
          areaData[2] = this.getDefaultColumns(areaData, 2)
          this.columns = areaData
          break
        case 3:
          if (this.form.nation) {
            this.backfill([this.form.nation], 'nation', 1)
            return
          }
          this.columns = [this.nation]
          break
        case 4:
          if (this.form.education_level) {
            this.backfill([this.form.education_level], 'educationLevel', 1)
            return
          }
          this.columns = [this.educationLevel]
          break
        case 5:
          if (this.form.level) {
            this.backfill([this.form.level], 'levelList', 1)
            return
          }
          this.columns = [this.levelList]
          break
        case 6:
          if (this.form.personnel_form) {
            this.backfill([this.form.personnel_form], 'personnel', 1)
            return
          }
          this.columns = [this.personnel]
          break
        case 7:
          if (this.form.area_range) {
            this.backfill([this.form.area_range], 'areaRangeList', 1)
            return
          }
          this.columns = [this.areaRangeList]
          break
        default:
          break
      }
    },
    async getAreaOption() {
      let setting = await this.$api.getAreaSetting({})
      let params = {
        high_level: setting.data.show_high_level,
        level: setting.data.show_low_level,
        with_up: 0,
      }
      const res = await this.$api.getCompanyAreaByAccount(params)
      this.pickSource.areaGroup = formatTreeOption(
        res.data,
        ['name', 'company_area_id', 'name', 'company_area_id', 'next', 'name', 'company_area_id'],
        3,
      )
      this.areaGroupList = flattenTree(this.pickSource.areaGroup)
    },
    handlePickerConfirm(e) {
      this.showPicker = false
      let type = this.currentPick
      let tag_ids = []
      let pickObj = e
      let labelString = pickObj.value
        .map((item) => {
          tag_ids.push(item.id)
          return item.label
        })
        .toString()
      switch (type) {
        case 1:
          // this.form.tagIdsStr = labelString;
          // this.form.tag_ids = tag_ids.toString();
          if (pickObj.value[pickObj.value.length - 1].id != '-') {
            this.form.tag_ids = pickObj.value[pickObj.value.length - 1].id
            this.form.tagIds = tag_ids
            this.form.tagIdsStr = pickObj.value[pickObj.value.length - 1].label
          } else {
            this.form.tag_ids = ''
            this.form.tagIdsStr = ''
            this.form.tagIsNone = tag_ids
          }
          break
        case 2:
          // 范围
          this.form.tagGroup = pickObj.value
            .map((item) => {
              return item.label
            })
            .join('/')
          // this.form.province = pickObj.value[0].id
          // this.form.city = pickObj.value[1].id
          // this.form.county = pickObj.value[2].id
          this.form.company_area_id =
            pickObj.value.length > 0 ? pickObj.value[pickObj.value.length - 1].id : ''
          break
        case 3:
          // 民族
          this.form.nation = pickObj.value[0].id
          this.form.nationStr = pickObj.value[0].label
          break
        case 4:
          // 文化程度
          this.form.education_level = pickObj.value[0].id
          this.form.educationLevelStr = pickObj.value[0].label
          break
        case 5:
          // 当前指导员级别
          this.form.level = pickObj.value[0].id
          this.form.levelStr = pickObj.value[0].label
          break
        case 6:
          // 人员构成
          this.form.personnel_form = pickObj.value[0].id
          this.form.personnelFormStr = pickObj.value[0].label
          break
        case 7:
          // 地区分布
          this.form.area_range = pickObj.value[0].id
          this.form.areaRangeStr = pickObj.value[0].label
          break
        default:
          break
      }
    },
  },
}
</script>

